'use client'

import { motion } from 'motion/react'
import { useState, useMemo } from 'react'
import { Video, Users, Sparkles, Play, Star, MessageCircle, Globe } from 'lucide-react'
import { useRouter } from 'next/navigation'

export default function HomePage() {
  const [isHovered, setIsHovered] = useState(false);
  const router = useRouter();

  // Generate deterministic star positions to avoid hydration mismatch
  const starPositions = useMemo(() => {
    const positions = []
    for (let i = 0; i < 20; i++) {
      // Use a simple deterministic function based on index
      const top = ((i * 37) % 100)
      const left = ((i * 73) % 100)
      positions.push({ top, left, delay: (i * 0.15) % 3 })
    }
    return positions
  }, [])

  return (
    <div className="min-h-screen bg-gradient-to-br from-purple-900 via-blue-900 to-indigo-900 relative overflow-hidden">
      {/* Animated Background Elements */}
      <div className="absolute inset-0 overflow-hidden">
        {/* Floating Shapes */}
        <motion.div
          className="absolute top-1/4 left-1/4 w-32 h-32 bg-gradient-to-r from-pink-400 to-purple-500 rounded-full blur-xl opacity-20"
          animate={{
            y: [0, -20, 0],
            rotate: [0, 5, -5, 0]
          }}
          transition={{
            duration: 4,
            repeat: Infinity,
            ease: "easeInOut"
          }}
        />
        <motion.div
          className="absolute top-3/4 right-1/4 w-24 h-24 bg-gradient-to-r from-cyan-400 to-blue-500 rounded-full blur-xl opacity-20"
          animate={{
            y: [0, -20, 0],
            rotate: [0, 5, -5, 0]
          }}
          transition={{
            duration: 4,
            repeat: Infinity,
            ease: "easeInOut",
            delay: 2
          }}
        />
        <motion.div
          className="absolute top-1/2 right-1/3 w-16 h-16 bg-gradient-to-r from-yellow-400 to-orange-500 rounded-full blur-lg opacity-30"
          animate={{
            y: [0, -20, 0],
            rotate: [0, 5, -5, 0]
          }}
          transition={{
            duration: 4,
            repeat: Infinity,
            ease: "easeInOut",
            delay: 4
          }}
        />
        
        {/* Stars */}
        {starPositions.map((star, i) => (
          <motion.div
            key={i}
            className="absolute w-1 h-1 bg-white rounded-full"
            style={{
              top: `${star.top}%`,
              left: `${star.left}%`,
            }}
            animate={{
              scale: [1, 1.2, 1],
              opacity: [0.7, 1, 0.7]
            }}
            transition={{
              duration: 2,
              repeat: Infinity,
              ease: "easeInOut",
              delay: star.delay
            }}
          />
        ))}
      </div>

      <motion.div
        className="relative z-10 container mx-auto px-6 py-12"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delayChildren: 0.3, staggerChildren: 0.2 }}
      >
        {/* Header */}
        <motion.header 
          className="text-center mb-16" 
          initial={{ y: 50, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ type: "spring", damping: 12, stiffness: 100 }}
        >
          <motion.div
            className="inline-flex items-center gap-3 mb-6"
            whileHover={{ scale: 1.05 }}
          >
            <motion.div
              className="p-3 bg-gradient-to-r from-pink-500 to-violet-500 rounded-full"
              animate={{
                boxShadow: [
                  "0 0 20px rgba(236, 72, 153, 0.3)",
                  "0 0 40px rgba(236, 72, 153, 0.6)",
                  "0 0 20px rgba(236, 72, 153, 0.3)"
                ]
              }}
              transition={{
                duration: 3,
                repeat: Infinity,
                ease: "easeInOut"
              }}
            >
              <Video className="w-8 h-8 text-white" />
            </motion.div>
            <h1 className="text-6xl md:text-8xl font-black bg-gradient-to-r from-pink-400 via-purple-400 to-cyan-400 bg-clip-text text-transparent tracking-tight">
              OMICRON
            </h1>
          </motion.div>
          
          <motion.p 
            className="text-xl md:text-2xl text-purple-200 font-light tracking-wide"
            initial={{ y: 50, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ type: "spring", damping: 12, stiffness: 100, delay: 0.1 }}
          >
            Connect • Discover • Experience
          </motion.p>
        </motion.header>

        {/* Main Content */}
        <motion.div 
          className="grid lg:grid-cols-2 gap-16 items-center"
          initial={{ y: 50, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ type: "spring", damping: 12, stiffness: 100, delay: 0.2 }}
        >
          {/* Left Side - Content */}
          <div className="space-y-8">
            <motion.h2 
              className="text-4xl md:text-5xl font-bold text-white leading-tight"
              initial={{ y: 50, opacity: 0 }}
              animate={{ y: 0, opacity: 1 }}
              transition={{ type: "spring", damping: 12, stiffness: 100, delay: 0.3 }}
            >
              Meet Amazing People
              <span className="block bg-gradient-to-r from-cyan-400 to-pink-400 bg-clip-text text-transparent">
                Around the World
              </span>
            </motion.h2>
            
            <motion.p 
              className="text-lg text-purple-200 leading-relaxed"
              initial={{ y: 50, opacity: 0 }}
              animate={{ y: 0, opacity: 1 }}
              transition={{ type: "spring", damping: 12, stiffness: 100, delay: 0.4 }}
            >
              Experience the magic of spontaneous connections through high-quality video calls. 
              Discover new cultures, make friends, and create unforgettable moments with strangers 
              who might become lifelong companions.
            </motion.p>

            {/* Features */}
            <motion.div 
              className="space-y-4"
              initial={{ y: 50, opacity: 0 }}
              animate={{ y: 0, opacity: 1 }}
              transition={{ type: "spring", damping: 12, stiffness: 100, delay: 0.5 }}
            >
              {[
                { icon: Globe, text: "Connect globally with anyone, anywhere" },
                { icon: Video, text: "Crystal clear HD video calls" },
                { icon: Users, text: "Safe and moderated community" },
                { icon: Sparkles, text: "AI-powered smart matching" }
              ].map((feature, index) => (
                <motion.div
                  key={index}
                  className="flex items-center gap-4 p-4 bg-white/10 backdrop-blur-sm rounded-xl border border-white/20"
                  whileHover={{ scale: 1.02, backgroundColor: "rgba(255,255,255,0.15)" }}
                  transition={{ type: "spring", stiffness: 300 }}
                >
                  <motion.div
                    className="p-2 bg-gradient-to-r from-pink-500 to-violet-500 rounded-lg"
                    animate={{
                      scale: [1, 1.2, 1],
                      opacity: [0.7, 1, 0.7]
                    }}
                    transition={{
                      duration: 2,
                      repeat: Infinity,
                      ease: "easeInOut"
                    }}
                  >
                    <feature.icon className="w-5 h-5 text-white" />
                  </motion.div>
                  <span className="text-white font-medium">{feature.text}</span>
                </motion.div>
              ))}
            </motion.div>
          </div>

          {/* Right Side - CTA */}
          <motion.div 
            className="text-center space-y-8"
            initial={{ y: 50, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ type: "spring", damping: 12, stiffness: 100, delay: 0.6 }}
          >
            {/* Anime-style character placeholder */}
            <motion.div
              className="relative mx-auto w-80 h-80 bg-gradient-to-br from-pink-400 via-purple-500 to-cyan-500 rounded-3xl shadow-2xl overflow-hidden"
              animate={{
                y: [0, -20, 0],
                rotate: [0, 5, -5, 0]
              }}
              transition={{
                duration: 4,
                repeat: Infinity,
                ease: "easeInOut"
              }}
              whileHover={{ scale: 1.05, rotateY: 5 }}
              style={{ 
                background: "linear-gradient(135deg, #667eea 0%, #764ba2 50%, #f093fb 100%)",
                boxShadow: "0 25px 50px -12px rgba(0, 0, 0, 0.5)"
              }}
            >
              {/* Decorative elements */}
              <div className="absolute inset-0 bg-gradient-to-br from-white/20 to-transparent" />
              <div className="absolute top-8 right-8">
                <motion.div 
                  animate={{
                    scale: [1, 1.2, 1],
                    opacity: [0.7, 1, 0.7]
                  }}
                  transition={{
                    duration: 2,
                    repeat: Infinity,
                    ease: "easeInOut"
                  }}
                >
                  <Star className="w-6 h-6 text-yellow-300 fill-current" />
                </motion.div>
              </div>
              <div className="absolute bottom-8 left-8">
                <motion.div 
                  animate={{
                    scale: [1, 1.2, 1],
                    opacity: [0.7, 1, 0.7]
                  }}
                  transition={{
                    duration: 2,
                    repeat: Infinity,
                    ease: "easeInOut",
                    delay: 1
                  }}
                >
                  <MessageCircle className="w-6 h-6 text-cyan-300" />
                </motion.div>
              </div>
              
              {/* Center content */}
              <div className="absolute inset-0 flex items-center justify-center">
                <motion.div
                  className="text-center"
                  whileHover={{ scale: 1.1 }}
                  transition={{ type: "spring", stiffness: 400 }}
                >
                  <Video className="w-16 h-16 text-white mx-auto mb-4" />
                  <p className="text-white font-bold text-xl">Ready to Connect?</p>
                </motion.div>
              </div>
            </motion.div>

            {/* CTA Button */}
            <motion.button
              className="group relative px-12 py-6 bg-gradient-to-r from-pink-500 via-purple-600 to-cyan-500 text-white font-bold text-xl rounded-2xl shadow-2xl transition-all duration-300"
              initial={{ y: 50, opacity: 0 }}
              animate={{ y: 0, opacity: 1 }}
              transition={{ type: "spring", damping: 12, stiffness: 100, delay: 0.7 }}
              whileHover={{ 
                scale: 1.05,
                boxShadow: "0 30px 60px -12px rgba(236, 72, 153, 0.5)"
              }}
              whileTap={{ scale: 0.98 }}
              onHoverStart={() => setIsHovered(true)}
              onHoverEnd={() => setIsHovered(false)}
            >
              <motion.div
                className="absolute inset-0 bg-gradient-to-r from-pink-400 via-purple-500 to-cyan-400 rounded-2xl opacity-0 group-hover:opacity-100 transition-opacity duration-300"
                animate={isHovered ? { scale: [1, 1.05, 1] } : {}}
                transition={{ duration: 0.6, repeat: Infinity }}
              />
              
              <span className="relative flex items-center gap-3 z-50" onClick={()=>{router.push("/chat")}}>
                <Play className="w-6 h-6 fill-current" />
                Start Chatting Now
                <motion.div
                  animate={{ rotate: isHovered ? 360 : 0 }}
                  transition={{ duration: 0.5 }}
                >
                  <Sparkles className="w-6 h-6" />
                </motion.div>
              </span>
              
              {/* Button glow effect */}
              <motion.div
                className="absolute inset-0 rounded-2xl"
                animate={{
                  boxShadow: [
                    "0 0 20px rgba(139, 69, 19, 0.3)",
                    "0 0 40px rgba(139, 69, 19, 0.6)",
                    "0 0 20px rgba(139, 69, 19, 0.3)"
                  ]
                }}
                transition={{
                  duration: 3,
                  repeat: Infinity,
                  ease: "easeInOut"
                }}
              />
            </motion.button>

            <motion.p 
              className="text-purple-300 text-sm"
              initial={{ y: 50, opacity: 0 }}
              animate={{ y: 0, opacity: 1 }}
              transition={{ type: "spring", damping: 12, stiffness: 100, delay: 0.8 }}
            >
              🚀 Join thousands of users already connecting worldwide
            </motion.p>
          </motion.div>
        </motion.div>

        {/* Footer Stats */}
        <motion.div 
          className="mt-20 grid grid-cols-1 md:grid-cols-3 gap-8 text-center"
          initial={{ y: 50, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ type: "spring", damping: 12, stiffness: 100, delay: 0.9 }}
        >
          {[
            { number: "50K+", label: "Active Users" },
            { number: "1M+", label: "Connections Made" },
            { number: "150+", label: "Countries" }
          ].map((stat, index) => (
            <motion.div
              key={index}
              className="p-6 bg-white/10 backdrop-blur-sm rounded-xl border border-white/20"
              whileHover={{ scale: 1.05, backgroundColor: "rgba(255,255,255,0.15)" }}
            >
              <motion.div
                className="text-4xl font-black bg-gradient-to-r from-pink-400 to-cyan-400 bg-clip-text text-transparent mb-2"
                initial={{ scale: 0 }}
                animate={{ scale: 1 }}
                transition={{ delay: index * 0.2, type: "spring", stiffness: 200 }}
              >
                {stat.number}
              </motion.div>
              <div className="text-purple-200 font-medium">{stat.label}</div>
            </motion.div>
          ))}
        </motion.div>
      </motion.div>
    </div>
  )
}